const Elgallery = document.querySelector('.gallery');
const markap = images.map(({preview , original , description}) =>`
    <li class="gallery-item">
	<a class="gallery-link" href="${original}">
		<img
		  class="gallery-image"
		  src="${preview}"
		  alt="${description}"
		/>
	</a>
</li>`
).join('');

Elgallery.innerHTML = markap;

new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
